import React, { useState } from 'react';

const Contact = () =>{

    const [data, setData] = useState({
        fullname: "",
        phone: "",
        email: "",
        msg: "",
    });

    const InputEvent = (event) =>{
        const {name, value} =event.target;
        setData((preVal) =>{
            return {
                ...preVal,
                [name] : value,
            }
        })
    }

    const formSubmit = (e) =>{
        e.preventDefault();
        alert(
            `My Name is ${data.fullname}. My Mobile Number is ${data.phone}. My email is ${data.email}, And what i say - ${data.msg}`
        );
    }

    return(
        <>
            <div className="my-5">
                <h1 className="text-center">Contact Us</h1>
            </div>
            <div className="container contact_div">
                <div className="row mb-5">
                    <div className="col-md-6 col-10 mx-auto">
                        <form onSubmit={formSubmit}>
                            <div className="mb-3">
                                <label for="FullName" className="form-label">Full Name</label>
                                <input type="text" className="form-control" id="FullName" value={data.fullname} name="fullname" onChange={InputEvent} placeholder="Enter Full Name" />
                            </div>
                            <div className="mb-3">
                                <label for="phone" className="form-label">Phone Number</label>
                                <input type="number" className="form-control" id="phone" value={data.phone} name="phone" onChange={InputEvent} placeholder="Phone Number" />
                            </div>
                            <div className="mb-3">
                                <label for="Email" className="form-label">Enter Email</label>
                                <input type="email" className="form-control" id="Email" value={data.email} name="email" onChange={InputEvent} placeholder="Enter Your Email" />
                            </div>
                            <div className="mb-3">
                                <label for="exampleFormControlTextarea1" className="form-label">Message</label>
                                <textarea className="form-control"  value={data.msg} name="msg" onChange={InputEvent} id="exampleFormControlTextarea1" rows="3"></textarea>
                            </div>
                            <div class="col-12">
                                <button class="btn btn-outline-primary" type="submit">Submit form</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Contact;